/*
* Sidebar Layout
*/

.rd-navbar-sidebar {
	display: block;

	.rd-navbar-sidebar-toggle {
		position: fixed;
		top: 8px;
		left: 8px;
		display: none;
		z-index: 999; // More than .rd-navbar-sidebar-inner
		@extend %rd-navbar-transition;
	}
}

.rd-navbar-sidebar .rd-navbar-sidebar-inner {
	position: absolute;
	display: flex;
	flex-direction: column;
	width: 100%;
	left: auto;
	max-width: 230px;
	height: calc(100vh - 215px);
	box-shadow: none;
	@extend %rd-navbar-transition;

	@media (min-width: $screen-lg-min) {
		max-width: 300px;
	}

	&:before, 
	&:after {
		content: '';
		display: block;
		height: 0;
	}

	&.affix {
		position: fixed;
		margin-top: 5px;
	}

	.rd-navbar-top-panel {
		order: 1;
		padding: 0 20px;
		text-align: left;
	}

	.rd-navbar-brand {
		margin-bottom: 30px;
	}

	// Base search style
	.rd-navbar-search {
		//display: block;
		display: inline-flex;
		width: 100%;
		//padding: 0 10px;

		.rd-search {
			position: relative;
			margin-top: 0;
			width: 100%;
		}

		.form-input {
			padding-right: 50px;
		}
		.form-wrap + * {
			margin-top: 0;
		}

		.rd-search-form-submit {
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			width: 50px;
			padding: 0;
			border: none;
			background-color: transparent;
			color: $gray-base;

			&:hover {
				color: $primary;
			}
		}

		.rd-search-results-live {
			display: none;
		}
	}

	// RD Navbar nav
	.rd-navbar-nav {
		display: block;
		font-size: 16px;
		line-height: 26px;
		text-align: left;

		&:before, 
		&:after {
			content: '';
			display: block;
			height: 8px;
		}
		
		> li > a {
			
		}

		li {
			> a {
				display: inline-block;
				padding: 11px 16px 11px 16px;
				color: $rd-navbar-fixed-nav-color;
				font-weight: 400;
				transition: .3s;
			

				&:hover {
					color: $rd-navbar-fixed-nav-active-background;
				}
			}

			&.active,
			&.opened {
				> a {
					color: $rd-navbar-fixed-nav-active-background;
				}
				> .rd-navbar-submenu-toggle {
					color: $rd-navbar-fixed-nav-active-background;
				}
			}

			&.opened {
				> .rd-navbar-submenu-toggle {
					&:after {
						transform: rotate(180deg);
					}
				}
			}
		}

		> li.opened {
			+ li {
				margin-top: 10px;
			}
		}

		.rd-navbar-submenu-toggle {
			display: none;
		}
	}

	* + .rd-navbar-nav-wrap {
		& {
			margin-top: 15px;
		}
	}

	// RD navbar submenu styles
	.rd-navbar-dropdown,
	.rd-navbar-megamenu {
		display: none;
	}

	.rd-navbar-submenu {
		position: relative;
		@extend %rd-navbar-transition;

		.rd-navbar-dropdown > li > a {
			padding-top: 7px;
			padding-bottom: 7px;
			font-size: 15px;
			font-weight: 400;
		}

		.rd-navbar-dropdown li li > a,
		.rd-navbar-megamenu ul li li > a {
			padding-left: 48px;
		}

		&.opened {
			> .rd-navbar-dropdown,
			> .rd-navbar-megamenu {
				display: block;
			}
		}
	}
}
