/*
*
* Lists
* --------------------------------------------------
*/

.list {
	> li + li {
		margin-top: 10px;
	}
}

.list-sm > li + li { margin-top: 10px; }

.list-md > li + li { margin-top: 18px; }

.list-lg > li + li { margin-top: 25px; }

@media (min-width: $screen-sm-min) {
	.list-xl > li + li { margin-top: 60px; }
}

// List inline
//

.list-inline {
	> li { display: inline-block; }
}

html {
	.list-inline-md {
		@include spacing(20px, 8px);

		@media (min-width: $screen-md-min) {
			@include spacingX(30px);
		}
	}
}

// List terms
//

.list-terms {
	dt + dd { margin-top: 5px; }
	dd + dt { margin-top: 25px; }
}

* + .list-terms { margin-top: 25px; }

// Index list
.index-list {
	counter-reset: li;

	> li {
		.list-index-counter {
			&:before {
				content: counter(li, decimal-leading-zero);
				counter-increment: li;
			}
		}
	}
}

/**
* Marked list
*/
.list-marked {
	padding-left: 0;
	text-align: left;
	@media (min-width: $screen-md-min) {
		padding-left: 25px;
	}

	> li {
		padding-left: 25px;
		text-indent: -25px;
		pre {
			text-indent: 0;
		}
		&::before {
			position: relative;
			display: inline-block;
			top: -2px;
			margin-right: 18px;
			content: '';
			width: 5px;
			height: 5px;
			border-radius: 100%;
			background-color: $gray-darker;
		}

		> .list-marked {
			padding-left: 15px;
			> li {
				&::before {
					background-color: transparent;
					border: 1px solid $gray-darker;
				}

				.list-marked {
					padding-left: 15px;
					> li {
						&::before {
							background-color: $gray-darker;
							border-radius: 0;
							border: 0;
						}
					}
				}
			}
		}
	}

	> li + li { margin-top: 4px; }
}

* + .list-marked { margin-top: 15px; }

p + .list-marked { margin-top: 10px; }

/**
* Ordered List
*/
.list-ordered {
	counter-reset: li;
	padding-left: 0;
	text-align: left;
	@media (min-width: $screen-md-min) {
		padding-left: 25px;
	}

	> li {
		position: relative;
		padding-left: 25px;

		&:before {
			content: counter(li, decimal) '.';
			counter-increment: li;
			position: absolute;
			top: 0;
			left: 0;
			display: inline-block;
			width: 15px;
			color: $gray-light;
		}

		> .list-marked {
			padding-left: 10px;
			> li {
				&::before {
					background-color: transparent;
					border: 1px solid $gray-darker;
				}

				.list-marked {
					padding-left: 10px;
					> li {
						&::before {
							background-color: $gray-darker;
							border-radius: 0;
							border: 0;
						}
					}
				}
			}
		}
	}

	> li + li { margin-top: 10px; }
}

* + .list-ordered { margin-top: 15px; }

ul {
	ul {
		padding-left: 30px;
	}
}

// Inline Term
.inline-term:before, 
.inline-term:after {
	display: table;
	content: "";
	line-height: 0; 
}

.inline-term:after {
	clear: both; 
}

.inline-term dt {
	font-weight: 700;
}

.inline-term dt, 
.inline-term dd {
	display: inline; 
}

.inline-term dt + dd {
	position: relative;
	padding-left: 20px; 
}

.inline-term dt + dd:before {
	content: '–';
	position: absolute;
	left: 5px;
}


// Marked List
.marked-list > li {
	position: relative;
	padding-left: 20px; 
}

.marked-list > li:before {
	content: '';
	position: absolute;
	top: 10px;
	left: 2px;
	width: 4px;
	height: 4px;
	border: 1px solid #000;
	border-radius: 50%; }

.marked-list + li {
	margin-top: 10px;
}
