/*
*
* Typography
* ==================================================
*/

// Body

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: 1.35;
  font-weight: $font-weight-base;
  color: $text-color;
  background-color: $body-bg;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: subpixel-antialiased;
  @media (min-width: $screen-xl-min) {
    line-height: $line-height-base;
  }
}

p {
  * {
    display: inline;
  }

  a {
    color: $primary;
  }

  [data-toggle='tooltip'] {
    padding-left: .25em;
    padding-right: .25em;
    color: $primary;
  }
}

a {
  color: $link-color;
  text-decoration: none;
  transition: .25s;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    outline: none;
  }

  &[href*='callto'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

.link-hover {
  color: $link-hover-color;
}

.link-press {
  color: $link-press-color;
}

//
// Headings
//
#{headings()} {
  margin-top: 0;
  margin-bottom: 0;
  color: $headings-color;

  a {
    color: inherit;

    &:hover {
      color: $primary;
    }
  }

  span {
    display: inline-block;
  }

  span[data-toggle='modal'] {
    border-bottom: 2px dashed rgba($primary, .3);
    cursor: pointer;
  }

  > span.icon {
    display: inline-block;
    margin-right: 5px;
  }
}

// {0 - 768px} <= 40px
// {768px - 992px} <= 62px
h1,
.heading-1 {
  font-size: 30px;
  line-height: 1.15;
  letter-spacing: 0;

  @media (min-width: $screen-xs-min) {
    font-size: 32px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 46px;
    line-height: 1.25;
  }

  @media (min-width: $screen-md-min) {
    font-size: 54px;
  }

  @media (min-width: $screen-lg-min) {
    font-size: 58px;
  }

  @media (min-width: $screen-xl-min) {
    font-size: $h1-font-size;
    line-height: $h1-line-height;
  }
}

// {0 - 768px} <= 36px
// {768px - 992px} <= 52px
h2,
.heading-2 {
  font-family: $font-family-sec;
  font-weight: 300;
  font-size: 28px;
  line-height: 1.4;
  letter-spacing: 0;

 @media (min-width: $screen-xs-min) {
    font-size: 30px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 44px;
  }

  @media (min-width: $screen-lg-min) {
    font-size: $h2-font-size;
    line-height: $h2-line-height;
  }
}

// {0 - 768px} <= 32px
// {768px - 992px} <= 44px
h3,
.heading-3 {
  font-size: 24px;
  line-height: 1.3;
  letter-spacing: 0;

  @media (min-width: $screen-sm-min) {
    font-size: 30px;
    line-height: 1.4;
  }

  @media (min-width: $screen-lg-min) {
    font-size: $h3-font-size;
    line-height: $h3-line-height;
  }

  .big {
    font-size: 2em;
    line-height: 1;
  }
}

// {0 - 768px} <= 26px
// {768px - 992px} <= 36px
h4,
.heading-4 {
  font-size: 18px;
  line-height: 1.5;
  letter-spacing: 0;

  @media (min-width: $screen-lg-min) {
    font-size: $h4-font-size;
    line-height: $h4-line-height;
  }

  &.h4-smaller {
    font-size: 19px;

    @media (min-width: $screen-lg-min) {
      font-size: 22px;
      line-height: (32 / 22);
    }
  }
}

// {0 - 768px} <= 22px
// {768px - 992px} <= 28px
h5,
.heading-5 {
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: .01em;

  @media (min-width: $screen-sm-min) {
    font-size: $h5-font-size;
    line-height: $h5-line-height;
  }
}

// {0 - 768px} <= 18px
// {768px - 992px} <= 22px
h6,
.heading-6 {
  font-size: 18px;
  line-height: 1.6;
}

// Big text
.big {
  font-size: $font-size-large;
}

// Small text
small,
.small {
  display: block;
  font-size: $font-size-small;
}

mark,
.mark {
  padding: 3px 5px;
  color: $white;
  background: $primary;
}

code {
  padding: 3px 5px;
  border-radius: $border-radius-small;
  color: $gray-dark;
  background: $gray-lighter;
}

pre {
  tab-size: 2;
}

.text-block {
  > * {
    margin-left: .125em;
    margin-right: .125em;
  }
}

// Logo
.brand {
  display: inline-block;
  .brand-logo-light {
    display: none;
  }
  .brand-logo-dark {
    display: block;
  }
}

.brand-logo {
  display: flex;
  align-items: center;
  font-family: $headings-font-family;
  letter-spacing: .04em;
  > * + * {
    margin-left: 5px;
  }
}

// section styles
.main-section {
  position: relative;
  min-height: calc(100vh - 80px);
}

.main-section-inner {
  border: 1px solid $gray-lighter;
  box-shadow: 0 0 25px rgba($gray-dark, .05);
  padding: 30px 15px;

  @media (min-width: $screen-sm-min) {
    padding: 45px 40px;
  }
}

// data-type 
* + [data-type='anchor'] {
  padding-top: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid $gray-lighter;
}

// boxes
.box {
  * + & {
    margin-top: 45px;
  }
}

* + .box-title {
  margin-top: 15px;
}

* + .box-content {
  margin-top: 15px;
}

// gallery item

.gallery-item {
  position: relative;
  display: inline-block;
  width: auto;
  box-shadow: 0 2px 10px 0 rgba(68, 73, 83, 0.12);
  
  img {
    width: 100%;
  }

  &:before {
    content: '\f002';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    z-index: 1;
    font: 400 20px/1 'FontAwesome';
    opacity: 0;
    color: $white;
    transition: 350ms ease-in-out;
  }
  &:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: '';
    opacity: 0;
    background: rgba($gray-darker, .3);
    transition: 350ms ease-in-out; 
  }

  &:hover {
    &:before,
    &:after {
      opacity: 1;
    }
  }
}
