/*
*
* Buttons
* --------------------------------------------------
*/

// Button General Styles

.button {
  position: relative;
  overflow: hidden;
  display: inline-block;
  @include btn-size(11px, 37px, $btn-font-size, $btn-lh-size);
  border: 2px solid;
  font-family: $font-family-base;
  font-weight: $btn-font-weight;
  letter-spacing: .1em;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  user-select: none;
  transition: 250ms all ease-in-out;
  text-transform: uppercase;
}

.button-block {
  display: block;
  width: 100%;
}

// Appearance style
// --------------------------------------------------

html {
  .button-default {
    @include btn-variant($gray-darker, $gray-lighter, $gray-lighter, $white, $primary, $primary);
  }

  .button-primary {
    @include btn-variant($white, $primary, $primary, $white, $gray-darker, $gray-darker);
  }

  // Outline
  .button-default-outline {
    @include btn-variant($gray-darker, transparent, $gray-lighter, $white, $primary, $primary);
  }

  .button-ghost {
    border: 0;
    background-color: transparent;

    &:hover {
      color: $white;
      background: $primary;
    }
  }
}

// Btn with shadow
.button-shadow {
  box-shadow: 0 9px 21px 0 rgba($gray-darker, 0.15);

  &:hover {
    box-shadow: 0 9px 10px 0 rgba($gray-darker, 0.15);
  }

  &:focus,
  &:active {
    box-shadow: none;
  }
}

// Button Sizes
// --------------------------------------------------

.button-xs {
  padding: 8px 17px;
}

.button-sm {
  @include btn-size(5px, 25px, 12px, 26px);
}

.button-lg {
  @include btn-size(12px, 50px, 14px, 28px);

  @media (min-width: $screen-md-min) {
    padding-left: 56px;
    padding-right: 56px;
  }
}

.button-xl {
  @include btn-size(16px, 50px, 16px, 28px);

  @media (min-width: $screen-md-min) {
    padding: 20px 80px;
  }
}

// Button Shapes
// --------------------------------------------------

.button-circle {
  border-radius: 30px;
}

.button-round-1 {
  border-radius: 5px;
}

.button-round-2 {
  border-radius: 10px;
}


