/*
*
* Utilities custom
* --------------------------------------------------
*/
.height-fill {
  position: relative;
  @include display-flex;
  @include flex-direction(column);
  @include align-items(stretch);

  > * {
    @include flex-grow(1);
    @include flex-shrink(0);
  }
}

// Object inline 
.object-inline {
  white-space: nowrap;
  > * {
    display: inline-block;
    min-width: 20px;
    vertical-align: top;
    white-space: normal;
  }

  > * + * {  margin-left: 5px; }
}

.oh {
  position: relative;
  overflow: hidden;
}

// Text decorations
.text-decoration-lines {
  position: relative;
  overflow: hidden;
  width: 100%;

  &-content {
    position: relative;
    display: inline-block;
    min-width: 170px;
    font-size: 13px;
    text-transform: uppercase;

    &::before,
    &::after {
      content: '';
      position: absolute;
      height: 1px;
      background: $gray-lighter;
      top: 50%;
      width: 100vw;
    }

    &::before {
      left: 0;
      transform: translate3d(-100%, 0, 0);
    }

    &::after {
      right: 0;
      transform: translate3d(100%, 0, 0);
    }
  }
}

* + .text-decoration-lines { margin-top: 30px; }

// Rights
p.rights {
  font-size: 14px;
  color: $text-color;
  a {  
    color: inherit;
    
    &:hover {
      color: $primary;
    } 
  }
}