/*
*
* Grid modules
* --------------------------------------------------
*/
.row-condensed {
  margin-left: 0;
  margin-right: 0;

  > [class*='col'] {
    padding-left: 0;
    padding-right: 0;
  }
}

// Grid demonstration
//
.grid-demonstration {
  $grid-heading: unquote("h5, .heading-5"); 
  padding: 12px 8px;
  letter-spacing: 0;
  text-align: left;

  #{$grid-heading} {
    font-weight: 700; 
  }

  @media (max-width: $screen-sm-max) {
    #{$grid-heading} {
      font-size: 14px;
    }
  }

  @media (max-width: $screen-md-max) {
    p {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

.container-fullwidth.grid-demonstration {
  @media (max-width: $screen-xs-max) {
    .row {
      margin-left: 0;
      margin-right: 0;
    }
  }
  
  [class^='col'] {
    padding: 5px;
    @media (max-width: $screen-xs-max) {
      p {
        font-size: 14px;
      }

      * + p { margin-top: 10px; }
    }

    @media (min-width: $screen-sm-min) {
      padding: 15px 10px;
    }

    @media (min-width: $screen-md-min) {
      padding: 25px 15px;
    }

    @media (min-width: $screen-lg-min) {
      padding: 30px 15px 30px 30px;
    }

    @media (min-width: $screen-xl-min) {
      padding: 50px 100px;

      * + p {
        margin-top: 24px;
      }
    }
  }

  .row {
    @media (min-width: $screen-xl-min) {
      margin-left: -70px;
      margin-right: -70px;
    }
  }
}

.grid-system-bordered {
  [class*='col'] {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .row {
    border-bottom: 1px solid $gray-lighter;
  }

  @media (min-width: $screen-sm-min) {
    [class*='col'] {
      padding-top: 25px;
      padding-bottom: 25px;
    }
  } 

  @media (min-width: $screen-lg-min) {
    [class*='col'] {
      padding-top: 45px;
      padding-bottom: 45px;
    }
  }
} 

.grid-system-outline {
  .row {
    border: 1px solid $gray-lighter;

    &:not(:last-child) { border-bottom-width: 0; }
  }

  [class*='col']:not(:last-child) {
    border-right: 1px solid $gray-lighter;
    margin-right: -1px;
  }

  * + .row { margin-top: 45px; }
  .row + .row { margin-top: 0; }
}