  //}

  //.unit#{$breakpoint}-grow-3{
  //  @include flex-grow(3);
  //}

  //.unit-item#{$breakpoint}-top{
  //  @include align-self(flex-start);
  //}

  //.unit-item#{$breakpoint}-middle{
  //  @include align-self(center);
  //}

  //.unit-item#{$breakpoint}-bottom{
  //  @include align-self(flex-end);
  //}

  //.unit-item#{$breakpoint}-narrow{
  //  @include flex-grow(0);
  //}

  //.unit-item#{$breakpoint}-standart{
  //  @include flex-grow(1);
  //}

  //.unit-item#{$breakpoint}-wide{
  //  @include flex-grow(2);
  //}

  .unit#{$breakpoint},
  .unit#{$breakpoint}-vertical {
    @include flex-direction(column);
    @include group($spacing-x, $spacing-y);

    > [class*='unit-']:first-child {
      padding-top: 0;
    }
  }

  .unit#{$breakpoint}-horizontal {
    @include flex-direction(row);
  }

  .unit#{$breakpoint}-inverse {
    &,
    &.unit#{$breakpoint}-vertical {
      @include flex-direction(column-reverse);
    }

    &.unit#{$breakpoint}-horizontal {
      @include flex-direction(row-reverse);
    }
  }
}

@mixin unit-spacing-variant($spacing-x, $spacing-y, $breakpoint) {
  &.unit#{$breakpoint},
  &.unit#{$breakpoint}-vertical {
    @include group($spacing-x, $spacing-y);
  }
}

@mixin unit-spacing($medias, $spacing-x, $spacing-y: $spacing-x) {
  @each $resolution, $alias in $medias {
    @if ($alias != '' and $alias != null) {
      $breakpoint: -#{$alias};
      @media (min-width: $resolution) {
        @include unit-spacing-variant($spacing-x, $spacing-y, $breakpoint);
      }
    } @else {
      $breakpoint: '';
      @include unit-spacing-variant($spacing-x, $spacing-y, $breakpoint);
    }
  }
} 
 
@mixin unit-responsive($medias, $spacing-x, $spacing-y: $spacing-x) {
  .unit {
    @include display-flex();
    @include flex(0 1 100%);
    @include flex-direction(column);
  }

  .unit-body {
    @include flex(0 1 auto);
  }

  .unit-left,
  .unit-right {
    @include flex(0 0 auto);
    max-width: 100%;
  }

  @each $resolution, $alias in $medias {
    @if ($alias != '' and $alias != null) {
      $breakpoint: -#{$alias};
      @media (min-width: $resolution) {
        @include unit-variant($spacing-x, $spacing-y, $breakpoint);
      }
    } @else {
      $breakpoint: '';
      @include unit-variant($spacing-x, $spacing-y, $breakpoint);
    }
  }
}