// Base styles
// --------------------------------------------------

.icon {
  display: inline-block;
  font-size: 16px;
  line-height: 1;
 
  &::before {
    position: relative;
    display: inline-block; 
    font-weight: 400;
    font-style: normal;
    speak: none; 
    text-transform: none;
  }
}

