//
// Flex grid offset
// --------------------------------------------------

@mixin responsive-offset($offset) {
  transform: translate3d(0, -$offset, 0);
  margin-bottom: -$offset;
  pointer-events: none;

  > *::before {
    content: '';
    display: table;
    width: 0;
  }

  > * {
    margin-top: $offset;
    pointer-events: auto;
  }
}

@mixin responsive-offset-media($offset) {
  transform: translate3d(0, -$offset, 0);
  margin-bottom: -$offset;

  > * {
    margin-top: $offset;
  }
}