/*
*
* Main layout
* ==================================================
*/

// Page
//

.page {
  position: relative;
  overflow: hidden;
  min-height: 100vh !important;
}

// Page header
//

.page-header {
  //Kill bootstrap style
  padding: 0;
  margin: 0;
  border-bottom: none;
}

// Page footer
//

.page-footer {
  padding-top: 40px;
  padding-bottom: 30px;
}

// Boxed Layout
// 
html.boxed {
  body {
    background: #f6f6f1 url(../images/bg-pattern-boxed.png) repeat fixed;
  }

  .page {
    max-width: $layout-boxed-width;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 0 23px 0 rgba(1, 1, 1, 0.1);
  }

  .rd-navbar-static,
  .rd-navbar-fullwidth {
    max-width: $layout-boxed-width;
    margin-left: auto;
    margin-right: auto;
    
    &.rd-navbar--is-stuck {
      max-width: $layout-boxed-width;
      width: 100%;
      left: calc(50% - #{$layout-boxed-width});
      right: calc(50% - #{$layout-boxed-width});
    }
  }
}