/*
* Static Layout
*/

$rd-navbar-static-width: $shell-lg-width;

.rd-navbar-static {
  display: block;

  .rd-navbar-aside-outer {
  }

  .rd-navbar-aside,
  .rd-navbar-main {
    max-width: $rd-navbar-static-width;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }

  .rd-navbar-aside {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .rd-navbar-main {
    position: relative;
    @include display-flex;
    @include align-items(center);
    @include justify-content(space-between);
    padding-top: 20px;
    padding-bottom: 20px;
  }

  // RD Navbar Brand
  .rd-navbar-brand {
    img {
      width: auto;
      height: auto;
      max-width: 200px;
    }
  }

  .rd-navbar-main-element {
    @include display-flex;
    @include align-items(center);
  }

  .rd-navbar-nav {
    > li + li {
      margin-left: $navbar-static-nav-indent;
    }
  }

  // RD Navbar Stick Up
  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone {
    .rd-navbar-aside-outer {
      display: none;
    }

    .rd-navbar-main {
      padding-top: 14px;
      padding-bottom: 14px;
    }
  }
}

