/*
*
* Offsets
* --------------------------------------------------
*/

// Elements offset
* + p { margin-top: 15px; }
* + form { margin-top: 20px; }
* + hr { margin-top: 20px; }
* + img { margin-top: 20px; }
* + pre { margin-top: 20px; }
* + video { margin-top: 20px; }


// Headings


// Headings + Paragraph
h2 + p, 
.heading-2 + p { 
  margin-top: 12px; 
}


// Paragraph + Headings
p + h2,
p + .heading-2 { 
  margin-top: 15px; 
}
* + h4{
  margin-top: 25px;
}
* + code, * + ul{
  margin-top: 15px;
}
// Tags + Tags
p + p { margin-top: 12px; }
img + p { margin-top: 15px; }
h3 + img { margin-top: 42px; }


// Classes
* + .big { margin-top: 20px; }
* + .text-block { margin-top: 30px; }
* + .button { margin-top: 30px; }
* + .gallery-item { margin-top: 20px; }
.gallery-item + * { margin-top: 20px; } 


// Classes + Tags
.range + h3,
.range + .heading-3 { 
  margin-top: 80px; 
}


// Classes + Classes
.shell + .shell { margin-top: 35px; }
.range + .range { margin-top: 35px; }


// Media offsets
@media (min-width: $screen-lg-min) {
  * + form { margin-top: 30px; }

  .shell + .shell { margin-top: 60px; } 
  .range + .range { margin-top: 60px; }
}

html {
  // Range spacing
  .range-0 { 
    @include responsive-offset(0px);
  }
  
  .range-15 { 
    @include responsive-offset(15px); 
  }
  
  .range-20 {
    @include responsive-offset(20px); 
  }
   
  .range-30 { 
    @include responsive-offset(30px);
  }
  
  .range-50 {
    @include responsive-offset(50px);
  }

  @media (min-width: $screen-md-min) {
    .range-md-30 {
      @include responsive-offset-media(30px);
    }
  }
}